<template>
	<div class="sensor-container">
		<div class="sensor-data">
			<div class="sensor-name-logo">
				<div class="logo">
					<img class="sensor-logo" :src="data.productLogo" />
				</div>
				<div class="client-name">
					{{ data.clientDisplayName }}
				</div>
			</div>
			<div class="sensor-value-unit">
				<div class="value">
					<template v-if="!data.isEnum">
						<IconNum v-if="data.value" :fsize="data.value | mathNumberSize" :num="data.value"></IconNum>
						<div v-else class="none-value">--</div>
					</template>
					<template v-else>
						<div class="enum-value">{{ data.value }}</div>
					</template>
				</div>
				<div v-if="data.numberUnit && data.value" class="unit">{{ data.numberUnit }}</div>
			</div>
		</div>
		<div class="sensor-history">
			<div @click="checkSensorHistory" class="history">
				<span class="iconfont icon-lishiquxian"></span>
				<span>历史数据</span>
			</div>
			<div v-if="data.valueLastTime" class="last-time">{{ data.valueLastTime | upToNowTime }}更新</div>
		</div>
	</div>
</template>

<script>
import IconNum from '@/components/common/iconNum.vue';
export default {
	props: {
		data: {
			type: Object
		}
	},
	components: {
		IconNum
	},
	filters: {
		mathNumberSize(val) {
			// 根据字的长度改变大小
			return String(val).length > 8 ? 18 : 22;
		}
	},
	methods: {
		checkSensorHistory() {
			const {deviceId, identifier, clientDisplayName, numberUnit, isEnum, specs} = this.data;
			this.$emit('checkSensorHistory', {deviceId, identifier, clientDisplayName, numberUnit, isEnum, specs});
		}
	}
};
</script>

<style lang="scss" scoped>
.sensor-container {
	width: 339px;
	height: 118px;
	padding: 0 5px 5px;
	background: url('../../../assets/images/iot/sensor_bg.png') no-repeat center center/ 100% 100%;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	.sensor-data {
		height: 77px;
		display: flex;
		align-items: center;
		padding: 0 15px;
		justify-content: space-between;
		border-bottom: 1px solid #00838f;
		.sensor-name-logo,
		.sensor-value-unit {
			display: flex;
			height: 30px;
			align-items: center;
			color: #adbec6;
			font-size: 16px;
			font-weight: bold;
			.logo {
				width: 30px;
				height: 30px;
				display: flex;
				align-items: center;
				justify-content: center;
				background: #00838f;
				border-radius: 30px;
				margin-right: 6px;
				.sensor-logo {
					width: 90%;
					height: 90%;
				}
			}
			.client-name {
				flex: 1;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				line-clamp: 2;
				-webkit-box-orient: vertical;
				line-height: 1.3;
			}
		}
		.sensor-value-unit {
			align-items: flex-end;
			font-weight: normal;
			line-height: 30px;
			.unit {
				margin-left: 6px;
				color: #999999;
				font-size: 14px;
				line-height: 2;
			}
			.none-value {
				font-size: 30px;
				color: #00838f;
			}
			.enum-value {
				font-size: 24px;
				color: #00ddaf;
			}
		}
	}
	.sensor-history {
		padding: 0 15px;
		flex: 1;
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-size: 14px;
		color: #00838f;
		.history {
			cursor: pointer;
			height: 100%;
			display: flex;
			align-items: center;
		}
	}
}
</style>



